/* ==================== Navbar / Universal ==================== */

nav {
	background-color: var(--nav-bg-color);
	height: min-content;
	color: var(--nav-font-color);
}

/* ==================== Navbar Centered ==================== */

.nav-centured-container {
	background-color: var(--nav-bg--color--dark-blue);
	padding: 2rem;
	margin: 0 auto;
}

.nav-centured--top-section {
	font-size: var(--nav-title--size);
	font-weight: var(--nav-title--weight);
	text-align: center;
}

.nav-centured--webname, .nav-centured--button {
	color: var(--body-bg--color);
	text-decoration: none;
}

.nav-centured--bottom-section {
	font-size: var(--nav-font--size);
	font-weight: var(--nav-font--weight);
	padding: 10px 0 0 0;
	display: flex;
	flex-direction: row;
	justify-content: center;
	gap: 1rem;
	align-items: flex-start;
}

.nav-centured--icons {
	display: flex;
	align-items: center;
}

.nav-centured--button-icon {
	margin: 0 0 0 15px;
	width: var(--nav-icon--size);
}

/* ==================== Navbar Minimal ==================== */

.NavbarMinimal {
	padding: var(--nav-padding);
	max-width: 800px;
	margin: 0 auto 0 auto;
}

.nav-minimal--container {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
}

.nav-minimal--title-section {
}

.nav-minimal--links-section {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: center;
	gap: var(--nav-padding);
}

.nav-minimal--title, .nav-minimal--page-link {
	text-decoration: none;
	color: var(--nav-font--color);
	text-shadow: 0 0 2px gray;
}

.nav-minimal--title {
	font-size: var(--nav-title--size);
	font-weight: var(--nav-title--weight);
}

.nav-minimal--page-link {
	font-size: var(--nav-font--size);
	font-weight: var(--nav-font--weight);
}

.nav-minimal--icons {
	display: flex;
	align-items: center;
	gap: 1rem;
}

.nav-minimal--icon {
	display: flex;
	width: var(--nav-icon--size);
}

/* More menu for Mobile */

.nav-minimal--button--more, .nav-minimal--button--exit {
	padding: 0;
	cursor: pointer;
	border: none;
	width: 2rem;
	height: 2rem;
	background-color: transparent;
}

.nav-minimal--button--more-icon, .nav-minimal--button--exit-icon {
	width: 100%;
}

.nav-minimal--more-section {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-color: var(--body-bg--color);
	display: flex;
	flex-direction: column;
	justify-content: space-evenly;
	align-items: center;
	z-index: 10;
}

.nav-minimal--more-top {
	position: fixed;
	top: var(--nav-padding);
	width: 100%;
	display: flex;
	width: calc(100% - 2 * var(--nav-padding));
	flex-direction: row;
	justify-content: space-between;
	align-items: center;
}

.nav-minimal--more-center {
	display: flex;
	flex-direction: column;
	gap: 2rem;
	align-items: center;
}

.nav-minimal--more-icons {
	position: fixed;
	bottom: 3rem;
	display: flex;
	align-items: center;
	gap: 2rem;
}

@media only screen and (max-width: 720px) {
	.NavbarMinimal {
		margin-bottom: 0rem;
	}
	.nav-minimal--container {
		align-items: center;
	}
}

/* ==================== Navbar CLI ==================== */

.NavbarCLI {
	background-color: black;
	height: min-content;
	color: white;
	width: 100%;
}

.nav-cli-container {
	height: min-content;
	color: white;
	font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
	text-shadow: 0 0 4px white;
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	padding: 2rem;
}

.nav-cli--top-section {
}

.nav-cli--bottom-section {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	width: 50%;
}

.nav-cli--goto {
	display: flex;
	flex-direction: row;
	justify-content: space-around;
	gap: 1rem;
}

.nav-cli--icons {
	display: flex;
	align-items: center;
	justify-content: center;
}

.nav-cli--cursor {
	background-color: white;
	animation: cursor-blink 1s infinite;
	box-shadow: 0 0 4px white;
}

.webname-cli, .nav-cli--button {
	color: white;
	text-decoration: none;
}

.nav-cli--icons {
	display: flex;
	align-items: center;
	gap: 1rem;
}

.nav-cli--button {
}

.nav-cli--button-icon {
	width: 1.5rem;
}

@media only screen and (max-width: 720px) {
	.nav-cli-container {
		flex-direction: column;
		justify-content: space-between;
		padding: 2rem;
	}

	.nav-cli--bottom-section {
		position: relative;
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		width: 100%;
	}

	.nav-cli--icons {
		position: absolute;
		right: -1rem;
		top: -2.5rem;

		display: flex;
		flex-direction: column;
		/* align-items: flex-start; */
		gap: 0.5rem;
	}

	.nav-cli--button-icon {
		width: 1.2rem;
	}
}

/* ==================== Projects Plank ==================== */

.ProjectsPlank {
	margin: none;
}

.projects-plank--title {
	font-size: 1.5rem;
	font-weight: 550;
	padding: 0;
	margin: 0 0 1rem 0;
	text-shadow: 0 0 2px gray;
}

.cards-list-plank {
    display: flex;
	flex-direction: column;
}

.projects-plank--filter-section {
	max-width: var(--body--width);
	/* width: auto; */
	margin: 3rem auto;
	display: flex;
	flex-direction: row;
	justify-content: space-around;
	align-items: center;
	z-index: 99;
}

.projects-plank--button--change-style {
	position: relative;
	border: none;
	border-radius: 50px;
	padding: 0;
	background-color: transparent;
	height: 2rem;
	width: 2rem;
	transition-property: background-color, box-shadow;
	transition-timing-function: ease-in-out;
	transition-duration: var(--projects--filter--animation--speed);
}

.projects-plank--filter {
	padding: 0;
	margin: 0;
	width: 50%;
	display: flex;
	flex-direction: row;
	align-items: center;
	gap: 2rem;
}

.projects-plank--filter-title {
	font-size: 1.2rem;
	font-weight: 550;
	padding: 0;
	margin: 0;
	text-shadow: 0 0 2px gray;
}

.projects-plank--filter--list {
	display: flex;
	/* overflow-x: auto; */
	gap: 1rem;
}

.projects-plank--filter--list-item {
	background-color: darkgray;
	color: white;
	padding: 0.5rem;
	border-radius: 8px;
	font-weight: 650;
	font-size: 0.9rem;
	opacity: 0.3;
}

.projects-plank--filter--list-icon {
	height: 2.2rem;
	opacity: 0.3;
	transition: opacity .5s linear;
}


.projects-plank--button--change-style--icon-front {
	position: absolute;
	width: 160%;
	top: 0;
	left: 0;
	z-index: 2;
	transition-property: width, top, left;
	transition-timing-function: ease-in-out;
	transition-duration: var(--projects--filter--animation--speed);
}

.projects-plank--button--change-style--icon-back {
	position: absolute;
	width: 100%;
	top: -25%;
	left: -25%;
	z-index: 1;
	transition-property: width, top, left;
	transition-timing-function: ease-in-out;
	transition-duration: var(--projects--filter--animation--speed);
}

@media (pointer: fine) {
	.projects-plank--button--change-style--icon-front {
		animation: putButtonFrontTouch var(--projects--filter--animation--speed);
	}

	.projects-plank--button--change-style--icon-back {
		animation: putButtonBackTouch var(--projects--filter--animation--speed);
	}

	/* .projects-plank--button--change-style:hover .projects-plank--button--change-style--icon-front { */
	/* 	/1* width: 130%; *1/ */
	/* 	/1* top: 25%; *1/ */
	/* 	/1* left: 25%; *1/ */
	/* 	animation: putButtonFrontHover 0.2s infinite; */
	/* } */

	/* .projects-plank--button--change-style:hover .projects-plank--button--change-style--icon-back { */
	/* 	/1* width: 130%; *1/ */
	/* 	/1* top: -70%; *1/ */
	/* 	/1* left: -70%; *1/ */
	/* 	animation: putButtonBackHover 0.2s infinite; */
	/* } */

	.projects-plank--button--change-style:hover {
		/* background-color: rgba(20, 20, 0, 0.2); */
		/* box-shadow: 0 10px 30px 0 rgba(0, 0, 0, 0.7), 0 20px 40px 0 rgba(0, 0, 0, 0.5); */
	}

	.projects-plank--filter--list-item:hover {
		opacity: 1;
	}

	.projects-plank--filter--list-icon:hover {
		opacity: 1;
	}
}

@media (pointer: coarse) {
	.projects-plank--button--change-style--icon-front {
		animation: putButtonFrontTouch var(--projects--filter--animation--speed);
	}

	.projects-plank--button--change-style--icon-back {
		animation: putButtonBackTouch var(--projects--filter--animation--speed);
	}

}

@keyframes putButtonFrontHover {
	from {
	}
	to {
	}
}

@keyframes putButtonFrontTouch {
	0% {
		width: 100%;
		top: -25%;
		left: -25%;
	}
	20% {
		width: 115%;
		top: -70%;
		left: -70%;
	}
	100% {
		width: 160%;
		top: 0;
		left: 0;
	}
}

@keyframes putButtonBackTouch {
	0% {
		width: 160%;
		top: 0;
		left: 0;
	}
	20% {
		width: 115%;
		top: 45%;
		left: 45%;
		z-index: 1;
	}
	100% {
		width: 100%;
		top: -25%;
		left: -25%;
		z-index: 1;
	}
}

