/* ==================== Card Plank ==================== */

.CardPlank {
    font-size: 1.5rem;
    display: flex;
    flex-direction: row;
	height: 300px;
}

.card-plank--image-section {
	height: 100%;
	width: 50%;
	display: flex;
	justify-content: flex-end;
	align-items: center;
}

.card-plank--image-subsection {
	height: 100%;
	width: 500px;
	display: flex;
	justify-content: center;
	align-items: center;
}

.card-plank--image {
	max-height: 90%;
	max-width: 80%;
}

.card-plank--content-section {
	display: flex;
	flex-direction: column;
	width: 50%;
	align-content: space-around;
}

.card-plank--top-section {
	height: 35%;
	padding: 10px;
	display: flex;
	flex-direction: column;
	gap: 0.5rem;
}

.card-plank--title {
	font-size: 1.8rem;
	font-weight: 500;
}

.card-plank--description {
	font-size: 1.25rem;
}

.card-plank--bottom-section {
	height: 100%;
	display: flex;
	flex-direction: column;
}

.card-plank--tech {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	justify-content: flex-start;
	gap: 10px;
	padding: 10px;
}

.card-plank--tech-item {
	background-color: black;
	padding: 0.6rem;
	border-radius: 8px;
	font-weight: 600;
	font-size: 1rem;
}

.card-plank--tech-item--image {
	height: 3rem;
}

.card-plank--links {
	height: 100%;
	display: flex;
	justify-content: flex-start;
	align-items: flex-end;
	padding: 1rem;
	gap: 1.5rem;
}

.card-plank--link-icon {
	background-color: white;
	padding: 8px;
	border-radius: 10px;
	width: 3rem;
}

@media only screen and (max-width: 720px) {
	.CardPlank {
		flex-direction: column;
		font-size: 1rem;
		min-width: 90%;
		margin: 0;
		height: 100%;
	}

	.card-plank--title {
		font-size: 1.6rem;
	}

	.card-plank--image-section {
		width: 100%;
		height: 100%;
	}

	.card-plank--image-subsection {
		min-width: 90%;
		margin: auto;

		padding: 1.5rem 0 0 0;
		border-radius: 25px 0 0 25px;
	}

	.card-plank--image {
		width: 100%;

	}

	.card-plank--content-section {
		height: 50%;
		width: 100%;
	}

	.card-plank--description {
		font-size: 1.2rem;
	}

	.card-plank--tech-item--image {
		height: 3rem;
	}

	.card-plank--links {
		justify-content: center;
		gap: 1.5rem;
	}
