	width: 50%;
	display: flex;
	align-items: center;
	padding: 3%;
	border-radius: 25px 0 0 25px;
}

.card-compact--image {
	max-height: 100%;
	max-width: 100%;
}

.card-compact--content-section {
	display: flex;
	flex-direction: column;
	width: 50%;
}

.card-compact--top-section {
	padding: 10px;
	display: flex;
	flex-direction: column;
	gap: 0.5rem;
}

.card-compact--title {
	font-size: 1.8rem;
	font-weight: 500;
}

.card-compact--discription {
	font-size: 1rem;
}

.card-compact--bottom-section {
	height: 100%;
	display: flex;
	flex-direction: column;
}

.card-compact--tech {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	justify-content: flex-start;
	gap: 10px;
	padding: 10px;
}

.card-compact--tech-item {
	background-color: black;
	padding: 0.6rem;
	border-radius: 8px;
	font-weight: 600;
	/* font-size: 1.3rem; */
	/* font-size: 1rem; */
}

.card-compact--tech-item-image {
	/* background-color: white; */
	padding: 2px;
	/* border-radius: 15px; */
	/* width: 3rem; */
	height: 3rem;
}

.card-compact--links {
	height: 100%;
	display: flex;
	justify-content: center;
	align-items: flex-end;
	padding: 10px;
	gap: 20px;
}

.card-compact--icon {
	background-color: white;
	padding: 8px;
	border-radius: 10px;
	width: 3rem;
}

