@media (pointer: fine) {
	.card-compact--icon {
		opacity: 0.4;
		transition: opacity 0.5s;
	}

	.card-compact--icon:hover {
		opacity: 1;
	}
}

@media (pointer: coarse) {
	.card-compact--icon {
		/* opacity: 0.9; */
		/* transition: opacity 0.8s, box-shadow 0.8s; */
		transition: box-shadow 0.8s;
	}

	.card-compact--icon:hover {
		/* opacity: 1; */
		box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
	}
}

/* ==================== ALGORITHMS ==================== */

.Algorithms {
	margin: 2rem auto 0 auto;
	max-width: var(--body--width);
	display: flex;
	flex-direction: column;
}

.algorithms--title {
	padding: 0;
	margin: 0 0 2rem 0;
	font-size: 1.5rem;
	font-weight: 550;
	text-shadow: 0 0 2px gray;
}

.algorithms--button-container {
	display: flex;
	flex-direction: row;
	justify-content: flex-start;
}

.algorithms--button-active, .algorithms--button-idle {
	margin: 0px;
	border: 0;
	font-size: 1.5rem;
	color: black;
	font-weight: 600;
	border-radius: 15px 15px 0 0;
	padding: 1rem 1.5rem 0.5rem 1.5rem;
}

.algorithms--button-active {
	background-color: var(--body-bg--color);
	box-shadow: 0 8px 1px 0 rgba(0, 0, 0, 0.2), 0 6px 10px 5px rgba(0, 0, 0, 0.19);
	clip-path: inset(-20px -20px 0 -20px);
	z-index: 5;
}

.algorithms--button-idle {
	background-color: darkgray;
	transition: background-color 0.4s ease;
}

.algorithms--button-idle:hover {
	background-color: lightgray;
}

.algorithms--container {
	border-top: 5px solid var(--body-bg-color);
	box-shadow: 0 1px 1px rgba(0, 0, 0, 0.2), -20px 8px 10px 10px rgba(0, 0, 0, 0.19);
	clip-path: inset(-20px 0 0 0);
	z-index: 1;
}

@media only screen and (max-width: 720px) {
	.Algorithms {
		padding-left: 1rem;
		padding-right: 1rem;
	}
}

/* ==================== SORTING ==================== */

#sortingCanvas {
	margin-top: 1rem;
	margin-bottom: 1rem;
	/* padding: 0 8px 0 8px; */
}

.controls {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
}

.section--sort {
	margin: 0px 0px 10px;
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	gap: 5px;
	justify-content: space-between;
}

.section--generate {
	/* max-width: var(--body-width); */
	display: flex;
	align-items: center;
	background-color: lightgray;
	padding: 10px
}

.section--discription {
	max-width: var(--body--width);
}
.button--algo {
	background-color: darkgray;
	margin: 0px;
	border: 0;
	border-radius: 5px;
	padding: 8px 10px;
	font-size: 1rem;
	color: white;
}

.button--algo:hover {
	opacity: 0.7;
}

.button--algo:active {
	background-color: indianred;
}

.button-selected {
	background: indianred;
}

.button-selected:hover {
	background: indianred;
}

.button--refresh {
	padding: 5px 10px;
	border-radius: 5px;
	width: 2rem;
	height: 2rem;
	background-image: url('./images/refresh-white.png');
	background-repeat: no-repeat;
	background-position: center;
	background-size: 65%;
}

.slider-container {
  width: 100%;
  padding: 0 10px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}

.slider {
  -webkit-appearance: none;
  width: 90%;
  height: 10px;
  border-radius: 5px;
  background: white;
  outline: none;
  opacity: 0.7;
  -webkit-transition: .2s;
  transition: opacity .2s;
}

.slider:hover {
  opacity: 1;
}

.slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 25px;
  height: 25px;
  border-radius: 50%;
  background: #008B8B;
  cursor: pointer;
}

.slider::-moz-range-thumb {
  width: 25px;
  height: 25px;
  border-radius: 50%;
  background: #04AA6D;
  cursor: pointer;
}

.slider-icons {
	margin: 0;
	width: 24px;
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: flex-end;
}

/* ==================== Archive ==================== */

.Archive {
	width: var(--body--width);
	margin: 0 auto;

}

.archive-container {
	display: flex;
	flex-direction: column;
	gap: 10px;
	padding-left: 20px;
}

.archive--title {
	margin: 1rem 0 0 0;
	padding: 0;
	font-size: 1.5rem;
	font-weight: 550;
	text-shadow: 0 0 2px gray;
}

.archive--link {
	text-decoration: none;
	font-size: 1.5rem;
	color: black;
	font-weight: 500;
}

.archive--link:visited {
	color: darkgray;
}

.archive--li {
	padding-left: 3rem;
}


/* ==================== Settings ====================*/

.settings--button--enter, .settings--button--exit {
	border: none;
	padding: 0;
	background-color: transparent;
}

.settings--button--enter {
	width: var(--settings--icon--size);
	position: absolute;
	top: 0.2rem;
	left: 0.2rem;
	opacity: 0.03;
	transition: opacity 0.5s ease-in-out;
}

.settings--button--exit {
	width: var(--nav-title--size);
	position: absolute;
	/* position: fixed; */
	top: 1rem;
	right: 1rem;
	cursor: pointer;
}

.settings--button--icon {
	width: 100%;
}

.settings--button--enter:hover {
    animation: settings-icon-spin infinite 10s linear;
	opacity: 0.5;
}

@keyframes settings-icon-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.settings {
	position: fixed;
	top: calc(50% - 250px);
	left: calc(50% - 250px);
	min-width: 500px;
	min-height: 500px;
	background-color:rgba(100, 100, 100, 0.9);
	color: var(--settings-font--color);
	/* display: flex; */
	/* justify-content: center; */
	box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.2), 0 6px 8px 0 rgba(0, 0, 0, 0.19);
	z-index: 10;
}

.settings-rim {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-color:rgba(100, 100, 100, 0.2);
	/* background-color: rgba(255, 255, 255, .15); */  
	backdrop-filter: blur(5px);
	z-index: 1;

}

.settings-content-container {
	height: 500px;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
}

.settings-content--title {
	margin: 0;

}

.settings-content--text {
	padding: 2rem;

}

.settings-content--img {
	max-width: 3rem;

}

.settings-content--wip-scroller {
	position: fixed;
}

@media only screen and (max-width: 720px) {
	.settings {
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
	}

	.settings-content-container {
		width: 100vw;
		height: 100vh;
	}

	.settings--button--exit {
		position: fixed;
	}
}

/* ==================== Go Up Button ====================*/

.go-up-container {
	position: relative;
}

.go-up--button {
	background-color:rgba(100, 100, 100, 0.7);
	border: none;
	margin: 0;
	padding: 0;
	border-radius: 8px;
	width: var(--nav-title--size);
	position: fixed;
	bottom: var(--go-up-padding);
	right: var(--go-up-padding);
	opacity: 0.5;
	display: flex;
	justify-content: center;
	align-items: center;
	transition: opacity 0.5s ease-in-out;
}

.go-up--button--icon {
	width: 100%;
}

@media (pointer: fine) {
	.go-up--button:hover {
		animation: go-up-appear infinite 2s linear;
		opacity: 1;
	}
}

@keyframes go-up-appear {
	0%{
		transform: translateY(0rem);
	}
	25%{
		transform: translateY(-0.25rem);
	}
	75%{
		transform: translateY(0.25rem);
	}
	100%{
		transform: translateY(0rem);
	}
}


